.stay-container {
    display: flex;
    justify-content: center;
    margin-top: em(19.5px);
}

.stay-grid {
    display: grid;
    width: 100%;

    .carousel-container {

        button {
            display: flex;
            align-items: center;
            justify-content: center;

            &.react-multiple-carousel__arrow {
                background-color: white;
                opacity: 0.6;
                transition-duration: 0.6s;
                min-width: 30px;
                min-height: 30px;

                &:hover {
                    opacity: 1;
                    transition-duration: 0.6s;
                }
            }

            &.react-multiple-carousel__arrow::before {
                color: $clrBlackFont;
            }
        }

        .custom-dot {
            bottom: em(7px);
        }
    }
}

.stay-list {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: em(60px) em(24px);

    @include for-narrow-layout {
        grid-template-columns:
            repeat(2, 1fr)
    }

    @media (min-width: 950px) {
        grid-template-columns: repeat(3, 1fr);
    }

    @media (min-width: 1100px) {
        grid-template-columns: repeat(4, 1fr);
    }

    @media (min-width: 1640px) {
        grid-template-columns: repeat(5, 1fr);
    }

    @media (min-width: 1880px) {
        grid-template-columns: repeat(6, 1fr);
    }
}




img {
    width: 100%;
    aspect-ratio: 1.0550819672/1;
    object-fit: cover;
    border-radius: em(10px);
    cursor: pointer;
}

.wish-list {
    position: absolute;
    display: flex;
    justify-content: end;
    top: em(17px);
    right: em(17px);
    cursor: pointer;
}

.stay-small-details {
    margin-top: em(4.5px);

    .small-details-header {
        justify-content: space-between;
    }

    p {
        color: #717171;
        font-size: 15px;
        font-family: circularL;
    }

    .priceP {
        position: relative;
        font-weight: bold;
        color: #222;
        font-size: em(16px);
        top: em(5px);

        :first-child {
            font-family: circularH;
            font-size: em(17px);
        }

        span {
            color: #222222;
            font-weight: 0;
            font-size: em(15px);
            // font-family: Helvetica, sans-serif;
        }
    }
}

.loc {
    font-family: circularH;
    font-weight: 600;
    font-size: 15px;
}

.preview-rate {
    justify-content: space-between;
    gap: em(4px);
}

.my-heart {
    color: #41454fd2;
}